import numeral from "numeral";

export const ACCEPT_TOKEN_CONVERSION_RATIO = process.env.REACT_APP_TEST
  ? 1 / 1000
  : 1;
export const TIME_RATIO = process.env.REACT_APP_TEST ? 1 / 96 : 1;

export const MOBILE_BREAK_POINT = 765;

export const SECOND = 1 * TIME_RATIO;
export const MINUTES = SECOND * 60;
export const HOUR = MINUTES * 60;
export const DAY = HOUR * 24;
export const MONTH = DAY * 30;
export const YEAR = DAY * 365;

