export const DURATIONS = [
  { id: 1, duration: 3, unit: DAY, unitText: "Days" },
  { id: 2, duration: 7, unit: DAY, unitText: "Days" },
  { id: 3, duration: 15, unit: DAY, unitText: "Days" },
  { id: 4, duration: 30, unit: DAY, unitText: "Days" },
];

export const toPattern = (value, duration) => {
  const patternObj = Object.values(NFT_PATTERNS).find(
    (item) => item.VALUE === value && item.DURATION === duration
  );
  if (!patternObj) {
    return NFT_PATTERNS.PATTERN_UNKNOWN.PATTERN;
  }
  return patternObj.PATTERN;
};

export const fromPattern = (pattern) => {
  const patternObj = Object.values(NFT_PATTERNS).find(
    (item) => item.PATTERN === pattern
  );
  if (!patternObj) {
    return [null, null];
  }
  return { value: patternObj.VALUE, duration: patternObj.DURATION };
};

export const formatOutput = (item) => item || "-";

export const formatNumber = (num, formatString = "0,0[.]00") => {
  if (!num || parseFloat(num) === 0) {
    return "-";
  }
  return numeral(num).format(formatString);
};
