import { useEffect, useReducer } from "react";
import { ethers } from "ethers";

import { readContractReducer, FETCH_STATE } from "../utils/utils";
import { useAPYIndicatorContract } from "./useContract";
import { useIsMounted } from "./useIsMounted";
import { useUpdate } from "./useUpdate";

const decodeAPYRecords = (records) =>
  records.map((record) => {
    const { utils } = ethers;
    return {
      apy: record.apy.toNumber(),
      name: utils.parseBytes32String(record.name),
      output: Number(utils.formatEther(record.output)),
      staked: Number(utils.formatEther(record.staked)),
    };
  });

export const useAPYRecords = () => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: [],
  });
  const apyIndicatorContract = useAPYIndicatorContract();
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!apyIndicatorContract) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await apyIndicatorContract.records();
        if (isMounted.current) {
          dispatch({
            type: FETCH_STATE.SUCCESS,
            payload: decodeAPYRecords(data),
          });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [apyIndicatorContract, isMounted, updateByTimer]);

  return state;
};
