import React from "react";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";
import numeral from "numeral";
import { formatNumber } from "../../../../utils/utils";
import styles from "./NFTCard.module.scss";
import commonStyles from "../../../../common.module.scss";

const NFTCard = (props) => {
  const { info, record } = props;
  const { apy, output, staked } = record;
  const { name, link, img } = info;
  const { t } = useTranslation();
  return (
    <div className={styles.nftCard}>
      <div className={styles.imgWrapper}>
        <img src={`/static/images/projects/${img}Frame.png`} alt={name} />
      </div>
      <div className={styles.content}>
        <div className={styles.apy}>APY:{formatNumber(apy, "0[.]00%")}</div>
        <div className={styles.details}>
          <h6 className={commonStyles.mainText}>{t("totalInvested")}</h6>
          <p>{formatNumber(staked, "$0,0[.]00")}</p>
          <h6 className={commonStyles.mainText}>{t("totalReturn")}</h6>
          <p>{formatNumber(output, "$0,0[.]00")}</p>
        </div>
      </div>
    </div>
  );
};

NFTCard.propTypes = {
  info: PropTypes.objectOf(PropTypes.any).isRequired,
  record: PropTypes.objectOf(PropTypes.any),
};

NFTCard.defaultProps = {
  record: {},
};

export default NFTCard;
