import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import { List } from "antd";
import StatusCard from "./StatusCard/StatusCard";
import styles from "./StatInfo.module.scss";

const STATS = [
  {
    title: "totalInvested",
    dataKey: "staked",
    img: "staked",
    format: "$0,0[.]00",
  },
  {
    title: "totalReturn",
    dataKey: "output",
    img: "output",
    format: "$0,0[.]00",
  },
  { title: "apy", dataKey: "apy", img: "apy", format: "0[.]00%" },
];

const StatInfo = (props) => {
  const { classname, records } = props;
  const [summary, setSummary] = useState({});
  useEffect(() => {
    setSummary(
      records.reduce(
        (accum, item) => ({
          apy: accum.apy + item.apy,
          output: accum.output + item.output,
          staked: accum.staked + item.staked,
        }),
        { apy: 0, output: 0, staked: 0 }
      )
    );
  }, [records]);
  return (
    <div className={classNames(styles.statInfo, classname)}>
      <List
        grid={{
          // gutter: [16, 16],
          xs: 3,
          sm: 3,
          md: 3,
          lg: 3,
          xl: 3,
          xxl: 3,
        }}
        dataSource={STATS}
        renderItem={(item) => (
          <List.Item>
            <StatusCard info={item} data={summary[item.dataKey]} />
          </List.Item>
        )}
      />
    </div>
  );
};

StatInfo.propTypes = {
  classname: PropTypes.string,
  records: PropTypes.arrayOf(PropTypes.any).isRequired,
};

StatInfo.defaultProps = { classname: "" };

export default StatInfo;
